* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.container .calcContainer {
    width: 30%;
    height: 90%;
    box-shadow: 0 0 1rem rgba(0,0,0, 0.5);
    border-radius: 1rem;
    display: flex;
    display: flex;
    flex-direction: column;
    padding: 2rem;
}

.container .calcForm {
    display: flex;
    flex-direction: column;
}



.container .calcForm h2 {
    text-align: center;
    margin: 1rem  0;
}

.container .calcForm .formGroup {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
}

.container .calcForm .formGroup input {
    margin-top: .3rem;
    padding: .4rem;
    outline: none;
}

.container .calcForm button {
    margin-top: 1rem;
    padding: .4rem;
    border-radius: .3rem;
    border: none;
    background: #09c;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
    position: relative;
    cursor: pointer;
}

.container .calcForm button::after {
    content: "";
    width: 80%;
    height: 5px;
    background: #ccc;
    position: absolute;
    bottom: -100%;
    left: 50%;
    transform: translateX(-50%);
}

.error {
    width: 250px;
    background: rgb(252, 169, 169);
    position: absolute;
    top: 10%;
    z-index: 9;
    left: 50%;
    transform: translateX(-50%);
    border-radius: .5rem;
    padding: 1rem;
    box-shadow: 10px 10px 1rem rgba(0,0,0,0.5);
}

.error i {
    position: absolute;
    top: .2rem;
    right: .3rem;
    color: rgb(252, 100, 100);
    cursor: pointer;
}

.error h5{ 
    color: rgb(252, 100, 100);
}

.error p {
    color: #fff;
    font-weight: 700;
}

.resultContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    margin-top: 2rem;
}

.BMI {
    width: 80%;
    border-radius: 15rem;
    padding: .4rem;
    display: flex;
    justify-content: center;
    border: 1px solid #09c;
}

.healthSpan {
    color: #09c;
    font-weight: 700;
}

@media screen and (max-width: 900px){
    .container .calcContainer {
        width: 60%;
    }
}

@media screen and (max-width: 600px){
    .container .calcContainer {
        width: 70%;
    }
}

@media screen and (max-width: 450px){
    .container .calcContainer {
        width: 90%;
    }
}